import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import systemUser from './views/system/systemUser.vue'
import systemAuthority from './views/system/systemAuthority.vue'
import systemMenuTypes from './views/system/systemMenuTypes.vue'
import systemMenuModule from './views/system/systemMenuModule.vue'
import userManage from './views/user/userManage.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/',
        redirect: '/home', //
        component: Home,
        hidden: true,
        iconCls:'fa fa-home',
        children: [
            { path: '/home', component: Main, name: 'home',hidden:true}
        ]
    },
    {
        path: '/login',
        component: Login,// 
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path:'/user',
        name:'',
        component:Home,
        iconCls:'fa fa-user',
        children:[
            {path:'/user/userManage', name:'',component:userManage}
        ]
    },
    {
        path:'/operate',
        component: Home,
        name:'',
        iconCls:'fa fa-list-alt',
        children:[
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table},
            {path: '/operate/bootPage', name:'', component:Table}
        ]
    },
    {
      path:'/chat',
      component:Home,
      name:'',
      iconCls:'fa fa-smile-o ',
      children:[
          {path:'/chat/chatChat' , name:'1', component:Table }
      ]
    },
    {
        path:'/call',
        component:Home,
        name:'',
        iconCls:'fa fa-volume-control-phone',
        children:[
            {path:'/call/callCenter' , name:'', component:Table }
        ]
    },
    {
        path:'/',
        component:Home,
        name:'',
        iconCls:'fa fa-bell',
        children:[
            {path:'/secure/audit' , name:'', component:Table },
            {path:'/secure/monitor' , name:'', component:Table },
            {path:'/secure/sensitiveWords' , name:'', component:Table }
        ]
    },
    {
        path: '/system',
        component: Home,
        name:'',
        iconCls: 'fa fa-cog',
        children: [
            { path: '/system/systemUser', name:'', component: systemUser},
            // { path: '', name:'ttt', component: Table},
            { path: '/system/systemAuthority', name:'', component: systemAuthority},
            { path: '/system/systemMenuTypes', name:'', component: systemMenuTypes},
            { path: '/system/systemMenuModule', name:'', component: systemMenuModule},
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


